h1, h2, h3, h4, h5, h6 {
    clear: both;
    font-family: $font__header;
    line-height: $line-height-header;
}

// select elements forced to inherit from header font family
.site-title,
.main-navigation {
    font-family: $font__header;
}

h1,
.h1 {
    // @include font-size(3.157);
    font-size: 3.157em;
    @media #{$tablet__breakpoint} {
      font-size: 3.157em;

   }

}
@mixin heading-1() {
    //font-size: 3.157em;
    // @include font-size(3.157);
    font-size: 3.157em;
    @media #{$tablet__breakpoint} {
      font-size: 3.157em;

    }
}

h2,
.h2 {
   font-size: 2.369em;
    @media #{$tablet__breakpoint} {
      font-size: 2.369em;

    }
   // @include font-size(3.728);
}
@mixin heading-2() {
   //@include font-size(3.728);
    font-size: 2.369em;
}

h3,
.h3 {
    font-size: 1.777em;
    font-weight: normal;
}

h4,
.h4 {
    font-size: 1.333em;
}

h5 {
    font-size: 1em;
}

h6,
.h6 {
    text-transform: uppercase;
}

/* NOTE: write mixin function that can quickly calculate these header size values to use within site styles */

h6,
small,
.font-small
{
    font-size: 0.75em;
}

